<template>
  <div class="navBarPaddingTop" @touchend="scroll">
    <div id="seroll">1</div>
    <ystHeader nav-title="看看宝宝" show-back="true" show-more="true" @clickBack="clickBack" @clickMore="clickMore"></ystHeader>
    <div class="live-container" @click="clickLive(item)" v-for="(item,index) in liveList">
      <div class="live-title">{{item.title}}</div>
      <div class="live-image"  >
        <img class="live-post" :src="item.image"/>
        <img class="player-btn" src="../../assets/index/play_icon_default.png"/>
      </div>
    </div>
  </div>
</template>

<script>
  import ystHeader from '../../components/common/ystHeader';
  import { navgationBack, navigateTo } from '../../utils/mRouter';

  export default {
    data() {
      return {
        babyId: 0,
        liveList: [{ title: '教学室内', image: '' }, { title: '户外运动', image: '' },{ title: '教学室内', image: '' }, { title: '户外运动', image: '' },{ title: '教学室内', image: '' }, { title: '户外运动', image: '' }],
      };
    },
    components: { ystHeader },
    methods: {
      clickBack() {
        console.log(`back${this.$route.name}`);
        navgationBack(-1, this);
      },
      clickMore() {
        console.log('click more');
      },
      clickLive(item) {
        navigateTo('../yst_lookBaby_live/yst_lookBaby_live', 'yst_lookBaby_live', { title: item.title }, this);
      },
      scroll(e) {
        console.log(this.$el.scrollTop);
        // console.log(e.target.y);
        console.log(e);
        // console.log(this.$el.querySelector('.navBarPaddingTop').scrollTop);
      },
    },
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .navBarPaddingTop {
    height: 100%;
    overflow: scroll;
  }
  .live-container {
    position: relative;
    .wh(7.5,4.55);
  }
  .live-title {
    .wh(7.18,0.6);
    .line-height(0.6);
    .margin-left(0.24);
    .margin-top(0.1);
    .fontSize-two;

  }
  .live-image {
    position: relative;
    .wh(7.02,3.88);
    .margin-left(0.24);
    .borderRadius(0.08);
    background-color: yellowgreen;
  }

  .live-image .live-post {
    .wh(7.02,3.88);
    .borderRadius(0.08);

  }
  .live-image .player-btn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    .wh(0.6,0.6);
    .borderRadius(0.08);


  }
</style>



